import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Obras } from './../../../model/objetc/obras';
import { ObraService } from './../../../service/sigobra/obra.service';
import { Base } from '../../../model/base';


@Component({
  selector: 'app-obras',
  templateUrl: './obras.page.html',
  styleUrls: ['./obras.page.scss'],
})
export class ObrasPage implements OnInit {

  obra: Obras;
  obras: Array<Obras>;
  value: string = '';
  constructor(private base: Base, 
              private os: ObraService, 
              private router: Router) { }

  ngOnInit() {
   
  }
  
  ionViewWillEnter() {
    this.carregarObras(" ");
  }

  excluir(obra : Obras) {
    this.base.Confirma('Deseja excluir essa empresa', this.removerativo.bind(this), obra);
  }

  remover(obra : Obras) {
    this.base.present();
    return this.os.delete(obra).subscribe(
      data => {
        this.base.dismiss();
        this.base.menssagemSucesso('Empresa Excluida');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  empresa '+ obra.id + this.base.tratarErro(error));  
      }
    );
  }

  editar(obra : Obras) { 
    this.os.obra = obra;
    this.router.navigate(['obra-inserir']);   
  }
  
  carregarObras(value: string) {
    this.base.present();
    return this.os.pesquisar(value).subscribe(
      data => {
        this.obras = data as Array<Obras>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  Obras ' + this.base.tratarErro(error));
      }
    );
  }

  carregarObra(value: string) {
    this.base.present();
    return this.os.pesquisa(value).subscribe(
      data => {
        this.obras = data as Array<Obras>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  Obras ' + this.base.tratarErro(error));
      }
    );
  }

  inserir() {
    this.os.obra = new Obras;
    this.router.navigate(['obra-inserir']);
  }

  removerativo(obra : Obras) {
    this.obra= obra;
    this.base.present();  
    if ( (this.obra.ativo !== null) && (this.obra.ativo !== undefined)) {
      this.obra.ativo = false;
    }  
    return this.os.editarativo(this.obra).subscribe(
      data => {        
        this.base.dismiss();
        this.router.navigate(['obras']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  empresa '+ this.obra.id + this.obra.ativo 
        + this.base.tratarErro(error));  
      }
    );           
  }
}
